import React, {useState} from 'react'

function Section({sectionHeight, title, desc, attachment, templateNum, font, backgroundColor, titleColor, descColor, titleSize, applyButtonUse, applyButtonText, applyButtonColor, targets}) {
    const scrollDown = () => {
        targets.current.scrollIntoView({behavior: 'smooth'})
    };    

    switch(parseInt(templateNum)){
        case 1:
            return (
            <div className="main-page-section" style={{backgroundColor:`${backgroundColor}`, height:`${sectionHeight}px`}}>
                <div className="main-section-right">       
                        {attachment && 
                        <div>
                            <img src={attachment} className="col-image"/>
                        </div>
                        }
                </div>
                <div className="main-section-left">
                    <div className="middle-section">
                        <span className="one-section-big" style={{fontFamily:`${font}`, color:`${titleColor}`, fontSize:`${titleSize}px`}}>
                            <pre style={{fontFamily:`${font}`, lineHeight:'1.2'}}>
                                {title}
                            </pre>
                        </span>
                        <span className="one-section-small" style={{marginTop:'0%'}}>
                            <pre style={{color:`${descColor}`}} >
                                {desc}
                            </pre>
                        </span>
                        {applyButtonUse && 
                        <span className="apply-button" style={{backgroundColor:`${applyButtonColor}`}} onClick={e => scrollDown}>
                            {applyButtonText}
                        </span>
                        }
                    </div>  
                </div>
            </div>
            )
        case 2:
            return (
                <div className="main-page-section" style={{justifyContent:'center', backgroundColor:`${backgroundColor}`, height:`${sectionHeight}px`}}>
                    <div className="main-section-left" style={{display:'flex', justifyContent:'center', paddingLeft:'0px', width:'100%'}}>
                        <div className="middle-section" style={{display:'flex', justifyContent:'center', width:'100%'}}>
                            <span className="one-section-big" style={{textAlign:'center', fontFamily:`${font}`, color:`${titleColor}`, fontSize:`${titleSize}px`, width:'100%'}}>
                                <pre style={{fontFamily:`${font}`}}>
                                    {title}
                                </pre>
                            </span>
                            <span className="one-section-small" style={{textAlign:'center', width:'100%', marginTop:'0%'}}>
                                <pre style={{color:`${descColor}`,textAlign:'center'}}>
                                    {desc}
                                </pre>
                            </span>
                            {applyButtonUse && 
                            <span className="apply-button" style={{backgroundColor:`${applyButtonColor}`}} onClick={e => scrollDown}>
                                {applyButtonText}
                            </span>
                            }
                        </div>  
                    </div>
                </div>
            )
        case 3:
            return (
                <div className="main-page-section" style={{backgroundColor:`${backgroundColor}`, height:`${sectionHeight}px`}}>
                    <div className="main-section-left">
                        <div className="middle-section">
                            <span className="one-section-big" style={{fontFamily:`${font}`, color:`${titleColor}`, fontSize:`${titleSize}px`}}>
                                <pre style={{fontFamily:`${font}`}}>
                                    {title}
                                </pre>
                            </span>
                            <span className="one-section-small">
                                <pre style={{color:`${descColor}`}}>
                                    {desc}
                                </pre>
                            </span>
                            {applyButtonUse && 
                            <span className="apply-button" style={{backgroundColor:`${applyButtonColor}`}} onClick={e => scrollDown}>
                                {applyButtonText}
                            </span>
                            }
                        </div>  
                    </div>
                    <div className="main-section-right">       
                            {attachment && 
                            <div style={{paddingLeft:'50px'}}>
                                <img src={attachment} className="col-image"/>
                            </div>
                            }
                    </div>
                </div>
            )
    }
}

export default Section
