import InfoPage from '../components/CustomerPage/InfoPage';
import QuestionsPage from '../components/CustomerPage/QuestionsPage';
import L1 from '../components/LandingPages/L1/L1';

import { createBrowserHistory } from 'history';
const history = createBrowserHistory();

const AppRouter = () => {
return(
    <Router history={history}>
        <>
        <div className="Container" style={{ minHeight: 'calc(100vh - 80px)', zIndex:-1 }}>
        <Routes>
            <Route exact path="/" element={<MainPage history={history}/>} />
            <Route exact path="/v2" element={<MainPageV2 history={history}/>} />
            <Route path="/make" element={<MakePage  history={history}/>} />
            <Route path="/seeResponse" element={<ResponsePage/> }/>
            <Route path="/submit" element={<SubmitPage history={history}/>} />
            <Route path="/coupon" element={<CouponPage history={history}/>} />
            <Route path="/info" element={<InfoPage history={history} />} />
            <Route path="/questions" element={<QuestionsPage history={history} />} />
            <Route path="/khj6051" element={<L1 history={history} />} />
        </Routes>
        </div>
        </> 
    </Router>
)
}
export default AppRouter;