import React, {useEffect, useState, useRef} from 'react'
import './utils/MakePage.css'
import { dbService } from '../tools/fbase';
import {Link} from 'react-router-dom';
import Section from './utils/Section'
import SectionMake from './utils/Make/SectionMake'
import LastSection from './utils/LastSection'
import MakeNavBar from './utils/NavBar/MakeNavBar'
import MakeFooter from './utils/NavBar/MakeFooter'
import LastSectionMake from './utils/Make/LastSectionMake'
import MainSection from './utils/Make/MainSection'
import ModalMade from './utils/ModalMade'
import LoadingModal from './utils/Modal/LoadingModal'
import { Input, Tooltip, Button } from 'antd';
import './utils/MakeLanding.css'
import './utils/NavBar/MakeNavBar.css'
import Switch from '@mui/material/Switch';
import { alpha, styled } from '@mui/material/styles';
import { pink } from '@mui/material/colors';

import section0 from '../tools/img/section0.png';
import section1 from '../tools/img/section1.png';
import section3 from '../tools/img/section3.png';
import surfeelogo from '../tools/img/surfeelogo.png';


const GreenSwitch = styled(Switch)(({ theme }) => ({
    '& .MuiSwitch-switchBase.Mui-checked': {
      color: "#6B63F7",
      '&:hover': {
        backgroundColor: alpha("#6B63F7", theme.palette.action.hoverOpacity),
      },
    },
    '& .MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track': {
      backgroundColor: "#6B63F7",
    },
  }));

const label = { inputProps: { 'aria-label': 'Switch demo' } };

const MakePage = ({history}) => {
    const targets = useRef(null)
    const [device, setDevice] = useState(true)
    const [full, setFull] = useState(false)
    // 메인 세팅
    const [mainColor, setMainColor] = useState("#6B63F7");
    const [mainTitleColor, setMainTitleColor] = useState("#000000");
    const [urlId, setUrlId] = useState("");
    const [urlTitle, setUrlTitle] = useState("Surfee | Landing Page Saas");
    const [font, setFont] = useState("OTWelcomeBA");
    const [faviconAttachment, setFaviconAttachment] = useState(`${surfeelogo}`);

    // 섹션 1
    const [s1title, setS1title] = useState("랜딩페이지 제작부터\n데이터 분석까지\n빠르고 간단하게");
    const [s1titleColor, setS1titleColor] = useState("rgba(255,255,255,1)");
    const [s1desc, setS1desc] = useState("신규 서비스와 이벤트부터 스타트업 아이템 검증까지,\n코딩없이 랜딩페이지를 만들고 실시간 데이터 분석을 통해\n최고의 고객 전환율을 경험해 보세요.");
    const [s1descColor, setS1descColor] = useState("rgba(255,255,255,1)");
    const [s1titleSize, setS1titleSize] = useState(40);
    const [attachment1, setAttachment1] = useState(`${section0}`);
    const [s1template, setS1template] = useState(1);
    const [s1backgroundColor, setS1backgroundColor] = useState("#6B63F7")
    const [sectionHeight1, setSectionHeight1] = useState(500)
    const [s1applyButton, setS1applyButton] = useState("지금바로 시작하기");
    const [s1applyButtonUse, setS1applyButtonUse] = useState(false);
    const [s1applyButtonColor, setS1applyButtonColor] = useState("rgba(255,255,255,1)");
    // 섹션 2
    const [s2title, setS2title] = useState("코딩도 디자인도 필요없이\nOne-Stop으로\n랜딩페이지 제작하기");
    const [s2titleColor, setS2titleColor] = useState("rgba(0,0,0,1)");
    const [s2titleSize, setS2titleSize] = useState(30);
    const [s2desc, setS2desc] = useState("개발자 없이도, 외주 없이도 직접\n템플릿을 고르고 블로그를 꾸미듯\n쉽고 빠르게 랜딩페이지를 만들어보세요.");
    const [attachment2, setAttachment2] = useState(`${section1}`);
    const [s2template, setS2template] = useState(3);
    const [s2backgroundColor, setS2backgroundColor] = useState("rgba(255,255,255,1)")
    const [sectionHeight2, setSectionHeight2] = useState(300)

    const [s3title, setS3title] = useState("최적의 전환율을 위한\n데이터 분석 자료를\n실시간으로 확인하기");
    const [s3desc, setS3desc] = useState("조회수, 사용자 수, 전환율(CVR), 유입 채널 등\n고객의 반응을 확인할 수 있는 유의미한 데이터를\n개발자에게 요청할 필요없이 직접 확인해 보세요.");
    // 섹션3
    const [attachment3, setAttachment3] = useState(`${section3}`)


    // 라스트 신청 부분
    const [lasttitle, setLasttitle] = useState("One-stop 랜딩페이지 제작 툴,\n가장 먼저 이용해 보세요");
    const [lastdesc, setLastdesc] = useState("사전신청하신 분들에겐 오픈 시 사용 가능한\n일주일 무료 이용권이 제공됩니다.");
    const [lasttitleColor, setLasttitleColor] = useState("rgba(0,0,0,1)");
    const [lastdescColor, setLastdescColor] = useState("rgba(0,0,0,1)");
    const [lasttitleSize, setLasttitleSize] = useState(30);
    const [lasttemplate, setLasttemplate] = useState(1);
    const [lastbackgroundColor, setLastbackgroundColor] = useState("#6B63F7")
    const [lastapplyButton, setLastapplyButton] = useState("사전신청하고 혜택받기");
    const [lastApplies, setLastApplies] = useState(1);
    const [answers, setAnswers] = useState([])
    const [lastSectionHeight, setLastSectionHeight] = useState(500);

    // 네비게이션
    const [naviTitle, setNaviTitle] = useState("Surfee")
    const [naviColor, setNaviColor] = useState("rgba(255,255,255,1)")
    const [naviTitleColor, setNaviTitleColor] = useState("#6B63F7")
    const [naviButtonTitle, setNaviButtonTitle] = useState("사전신청 하기")
    const [naviButtonColor, setNaviButtonColor] = useState("#6B63F7")
    const [naviButtonTitleColor, setNaviButtonTitleColor] = useState("black")

    // 푸터
    const [footerOrNot, setFooterOrNot] = useState(false);
    const [footerColor, setFooterColor] = useState("white");
    const [userEmail, setUserEmail] = useState("surfee.business@gmail.com");
    const [userPhoneNumber, setUserPhoneNumber] = useState("010-4690-5086");

    // 사진들 아래는 기타
    const [secNum, setSecNum] = useState(0);
    const [secNums, setSecNums] = useState([0,1,2,3]);
    const [section, setSection] = useState(1);
    const [sections, setSections] = useState([1,2,3]);
    const [makes, setMakes] = useState([0,1,2]);
    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);
    // CTA
    const [ctaUse, setCtaUse] = useState(false);
    const [ctaTitle, setCtaTitle] = useState("CTA 버튼");
    const [ctaColor, setCtaColor] = useState("rgba(0,0,0,1)");
    const [ctaLink, setCtaLink] = useState("www.naver.com");

    const onSubmit = async () => {
        var result = window.confirm("진짜 완성했죠?");
        if(result){
            setLoading(true);

            // 사진을 먼저 업로드하고 그 URL을 받아서 데이터로 넣어줘야한다.
            // const attachmentRef = stService.ref().child(`${userObj.uid}/${uuidv4()}`)
            // const response = await attachmentRef.putString(attachment, "data_url");
            // const attachmentURL = await response.ref.getDownloadURL();

            const oneLandingPage = {
                created:Date.now(),
            };

            await dbService.collection("apply-landing").add(oneLandingPage);
            
            setTimeout(() => {
                setLoading(false);
                history.push('/#/submit');
                history.go();
            },2000)
        }else{
            return
        }
    }

    const onFileChange =  (e, num) => {
        console.log("e", num);
        const {target:{files},} = e;
        const oneFile = files[0];
        const reader = new FileReader();
        reader.onloadend = (finishedEvent) => { // 로딩이 끝날 때 실행한다는 뜻.
            const {currentTarget:{result}} = finishedEvent;
            switch (num){
                case 1:
                    setAttachment1(result);
                    break;
                case 2:
                    setAttachment2(result);
                    break;
                case 3:
                    setAttachment3(result);
                    break;

            }
        }
        reader.readAsDataURL(oneFile);
    }

    const doSave = () => {
        // 저장하기
        alert("오키")
    }

    const addSection = () => {
        if(sections.length > 4){
            alert("제작가능한 최대 섹션 개수에 한계가 있습니다!");
        }else{
            setSections([...sections, sections.length+1]);
        }
    }

    const deleteSection = () => {
        const secNumsTemp = sections;
        secNumsTemp.pop();
        setSections(secNumsTemp);
    }

    const addInput = () => {
        setLastApplies(lastApplies + 1);
        console.log(lastApplies + 1);
    }

    const selectorList = secNums.map((item,index) => {
        if(item === secNum){
            var borderBottom = '';
            var backColor = 'rgb(243, 243, 243)';
        }else{
            var borderBottom = '1px solid gray';
            var backColor = 'white';
        }
        if(item === 0){
            return (
                <span index={index} style={{borderBottom:`${borderBottom}`, backgroundColor:`${backColor}`}} className="make-page-section-selector" onClick={(e) => {
                    setSecNum(item);
                    }}>
                    기본세팅
                </span>
            )
        }else if(item === 1){
            return(
                <span index={index} style={{borderBottom:`${borderBottom}`, backgroundColor:`${backColor}`}} className="make-page-section-selector" onClick={(e) => setSecNum(item)}>
                    <span style={{width:'70%'}}>메인</span>
                </span>
                )
        }else if(item === 2){
            return(
                <span index={index} style={{borderBottom:`${borderBottom}`, backgroundColor:`${backColor}`}} className="make-page-section-selector" onClick={(e) => setSecNum(item)}>
                    <span style={{width:'70%'}}>세부 내용</span>
                </span>
                )
        }else if(item === 3){
            return(
                <span index={index} style={{borderBottom:`${borderBottom}`, backgroundColor:`${backColor}`}} className="make-page-section-selector" onClick={(e) => setSecNum(item)}>
                    <span style={{width:'70%'}}>신청</span>
                </span>
            )
        }
    })

    const makeSection = () => {
        switch(section){
            case 1:
                return(
                    <>
                    
                    <SectionMake 
                            title={s1title} desc={s1desc}  secNum="1" sectionHeight={sectionHeight1} 
                            attachment={attachment1} titleColor={s1titleColor} backgroundColor={s1backgroundColor}
                            setTitleColor={setS1titleColor} setTitleSize={setS1titleSize}
                            setTitle={setS1title} setDesc={setS1desc} 
                            onFileChange={onFileChange} setTemplate={setS1template}
                            setBackgroundColor={setS1backgroundColor} setSectionHeight={setSectionHeight1}
                            />
                            <div style={{display:'flex', justifyContent:'start', flexDirection:'column', width:'90%'}}>
                                <div style={{display:'flex', justifyContent:'start', flexDirection:'row', alignItems:'center', textAlign:'left', marginTop:'10px'}}>
                                    <span style={{fontSize:'16px'}}>■ 버튼 위치시키기</span>  
                                    <GreenSwitch {...label} value={s1applyButtonUse} onChange={e => setS1applyButtonUse(!s1applyButtonUse)} />
                                </div>
                                {s1applyButtonUse && <>
                                    1번에 버튼 텍스트
                                    <input value={s1applyButton} type="text" onChange={e => setS1applyButton(e.currentTarget.value)} className="section-one-title-input"/>
                                    <input value={s1applyButtonColor} type="color" onChange={e => setS1applyButtonColor(e.currentTarget.value)} className="section-one-title-input"/>
                                </>
                                }
                            </div>
                    </>
                )
            case 2:
                return(
                    <>
                    <SectionMake 
                            title={s1title} desc={s1desc}  secNum="2" sectionHeight={sectionHeight1} 
                            attachment={attachment1} titleColor={s1titleColor} backgroundColor={s1backgroundColor}
                            setTitleColor={setS1titleColor} setTitleSize={setS1titleSize}
                            setTitle={setS1title} setDesc={setS1desc} 
                            onFileChange={onFileChange} setTemplate={setS1template}
                            setBackgroundColor={setS1backgroundColor} setSectionHeight={setSectionHeight1}
                            />
                    </>
                )
        }
    }

    const selectorTable = () => {
        switch (secNum){
            case 0: // 기본 세팅
                return <>
                <MainSection
                    ctaUse={ctaUse} ctaLink={ctaLink} ctaTitle={ctaTitle} ctaUse={ctaUse} urlId={urlId} naviButtonColor={naviButtonColor} footerOrNot={footerOrNot}
                    mainColor={mainColor} naviTitle={naviTitle} naviTitleColor={naviTitleColor} naviButtonTitle={naviButtonTitle} mainTitleColor={mainTitleColor}
                    userEmail={userEmail} userPhoneNumber={userPhoneNumber} faviconAttachment={faviconAttachment} urlTitle={urlTitle}
                    naviColor={naviColor} setNaviColor={setNaviColor} footerColor={footerColor} setFooterColor={setFooterColor}
                    setFont={setFont} setCtaUse={setCtaUse} setCtaLink={setCtaLink} setMainColor={setMainColor}
                    setCtaColor={setCtaColor} setCtaTitle={setCtaTitle} setUrlId={setUrlId} setNaviButtonColor={setNaviButtonColor}
                    setNaviTitle={setNaviTitle} setNaviTitleColor={setNaviTitleColor} setNaviButtonTitle={setNaviButtonTitle}
                    setS1backgroundColor={setS1backgroundColor} setLastbackgroundColor={setLastbackgroundColor}
                    setS1titleColor={setS1titleColor} setS1descColor={setS1descColor}
                    setLasttitleColor={setLasttitleColor} setLastdescColor={setLastdescColor}
                    setMainTitleColor={setMainTitleColor} setFooterOrNot={setFooterOrNot} setUrlTitle={setUrlTitle}
                    setUserEmail={setUserEmail} setPhoneNumber={setUserPhoneNumber} setFaviconAttachment={setFaviconAttachment}
                />
                </>
            case 2:
                return <>
                <div style={{margin:'10px 0px', display:'flex', flexDirection:'row'}}>
                    {sections.map((item, index) => {
                        let selected = "rgba(0,0,0,0)";
                        if(item === section){
                            selected = "#6a63f76e"
                        }
                        return(
                            <button onClick={() => setSection(item)}index={index} className="make-which-section-making-button" style={{backgroundColor: `${selected}`}}>섹션 {item}</button>
                        )
                    })}
                    <button onClick={() => addSection()} className="make-which-section-making-add-button">+</button>
                    <button onClick={() => deleteSection()} className="make-which-section-making-add-button">-</button>
                </div>
                <div>
                    {makeSection()}
                </div>
                </>
            case 1:
                return <>
                <SectionMake 
                        title={s1title} desc={s1desc}  secNum="1" sectionHeight={sectionHeight1} descColor={s1descColor}
                        attachment={attachment1} titleColor={s1titleColor} backgroundColor={s1backgroundColor}
                        setTitleColor={setS1titleColor} setTitleSize={setS1titleSize}
                        setTitle={setS1title} setDesc={setS1desc} setDescColor={setS1descColor}
                        onFileChange={onFileChange} setTemplate={setS1template}
                        setBackgroundColor={setS1backgroundColor} setSectionHeight={setSectionHeight1}
                        />
                        <div className="make-section-one-part">
                            <div className="make-section-title" style={{flexDirection:'row', alignItems:'center'}}>
                                <span style={{fontSize:'16px'}}>■ 버튼 위치시키기</span>  
                                <GreenSwitch {...label} value={s1applyButtonUse} onChange={e => setS1applyButtonUse(!s1applyButtonUse)} />
                            </div>
                            {s1applyButtonUse && <>
                                1번에 버튼 텍스트
                                <input value={s1applyButton} type="text" onChange={e => setS1applyButton(e.currentTarget.value)} className="section-one-title-input"/>
                                <input value={s1applyButtonColor} type="color" onChange={e => setS1applyButtonColor(e.currentTarget.value)} className="section-one-title-input"/>
                            </>
                            }
                        </div>
                </>
            case 3:
                return <>
                <LastSectionMake 
                    title={lasttitle} desc={lastdesc}  lastApplies={lastApplies} answers={answers} lasttitleColor={lasttitleColor}
                    sectionHeight={lastSectionHeight}
                    setTitleColor={setLasttitleColor} setTitleSize={setLasttitleSize}
                    setTitle={setLasttitle} setDesc={setLastdesc} setSectionHeight={setLastSectionHeight}
                    setBackgroundColor={setLastbackgroundColor} setAnswers={setAnswers} 
                    addInput={addInput} setLasttitleColor={setLasttitleColor}
                />
                </>
        }
    }

    return (<>
       <MakeNavBar 
        open={open} setOpen={setOpen}
        full={full} setFull={setFull}
        device={device} setDevice={setDevice} doSave={doSave}
        naviColor={naviColor}
       />
        <div className="make-page-container" style={{marginTop:'-10px'}}>
        {/* 여기서부터 메인페이지 베껴옴 */}
        <div className="main-page-container" style={{width:`${full ? '100%' : '70%' }`,}}>  
            <div className="make-navigation" style={{width:`${full ? '100%' : '70%'}`,backgroundColor:`${naviColor}`}}>
                <span className="make-nav-bar-title" onClick={() => {history.push(`/#/${urlId}`); history.go();}} style={{font:`${font}`, cursor:'pointer', color:`${naviTitleColor}`}}>
                    {naviTitle}
                </span>
                <span className="nav-on">
                    <button className="nav-button" style={{backgroundColor:`${naviButtonColor}`, cursor:'pointer'}} onClick={() => window.scrollTo(0,document.body.scrollHeight)}>{naviButtonTitle}</button>
                </span>
            </div>     
            { sections.length > 1 && 
            <Section 
                title={s1title} desc={s1desc} attachment={attachment1}
                templateNum={s1template} font={font} backgroundColor={s1backgroundColor}
                titleColor={s1titleColor} titleSize={s1titleSize} descColor={s1descColor}
                applyButtonUse={s1applyButtonUse} applyButtonText={s1applyButton} applyButtonColor={s1applyButtonColor}
                targets={targets} sectionHeight={sectionHeight1}
            />
            }
            { sections.length > 2 && 
            <Section 
                title={s2title} desc={s2desc} attachment={attachment2} 
                templateNum={s2template} font={font} backgroundColor={s2backgroundColor}
                titleColor={s2titleColor} titleSize={s2titleSize}
                applyButtonUse={false} sectionHeight={sectionHeight2}
            />
            }
            { sections.length > 3 && 
            <Section title={s3title} desc={s3desc} attachment={attachment3} templateNum="1" font={font} />
            }
            <LastSection 
                targets={targets} lasttitleColor={lasttitleColor}
                title={lasttitle} desc={lastdesc} sectionHeight={lastSectionHeight}
                font={font} backgroundColor={lastbackgroundColor}
                titleColor={lasttitleColor} titleSize={lasttitleSize}
                applyButtonText={lastapplyButton} answers={answers} lastdescColor={lastdescColor}
            />
            {ctaUse && 
            <div className="make-cta-button" style={{backgroundColor:`${ctaColor}`}}>{ctaTitle}</div>
            }
            {footerOrNot ?  <></> : 
            <MakeFooter 
            userPhoneNumber={userPhoneNumber}
            userEmail={userEmail}
            naviTitle={naviTitle}
            footerColor={footerColor}
            />
            } 
        </div>
        {/* 여기까지 메인페이지 베껴옴 */}
            <div className="make-page-make-space" style={{display:`${full ? 'none' : 'flex'}`}}>
                <div className="section-selector-container">
                    {selectorList}
                </div>
                <div className="section-table-container" style={{width:'100%'}}>
                    {selectorTable()}
                </div>
                <div style={{display: 'flex', width:'80%', justifyContent: 'center', alignItems:'center', marginTop:'10%', position:'absolute', bottom:'70px'}}>
                    <Button onClick={onSubmit} style={{width:'90%', boxShadow:'1px 1px 3px rgba(0,0,0,0.2)'}}>등록</Button>
                    <ModalMade open={open} setOpen={setOpen}/>
                    <LoadingModal loading={loading} />
                </div>
            </div>
            {/* 로딩창 */}
        </div>
        </>)
}

export default MakePage
