import React, {useState, useEffect} from 'react'
import { styled, Box } from '@mui/system';
import ModalUnstyled from '@mui/base/ModalUnstyled';
import { Input, Tooltip, Button } from 'antd';

import img1 from '../../tools/info/001.png';
import img2 from '../../tools/info/002.png';
import img3 from '../../tools/info/003.png';
import img4 from '../../tools/info/004.png';
import img5 from '../../tools/info/005.png';

const StyledModal = styled(ModalUnstyled)`
  position: fixed;
  z-index: 1300;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Backdrop = styled('div')`
  z-index: -1;
  position: fixed;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-tap-highlight-color: transparent;
`;

const style = {
  width: 500,
  bgcolor: 'rgba(255,255,255,1)',
  border: '2px solid #000',
  p: 2,
  px: 4,
  pb: 3,
};

function ModalMade({open, setOpen}) {
    // 모달
    const [cnum, setCnum] = useState(1);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    useEffect(() => {
        setCnum(1);
    },[open]);

    const content = () => {
        switch(cnum){
            case 1:
                return(
                    <div style={{display:'flex', flexDirection:'column'}}>
                        <div>{cnum} /5</div>
                        <img src={img1} />
                        <div style={{display:'flex', flexDirection:'row'}}>
                            <Button onClick={e => setCnum(cnum + 1)}>다음</Button>  
                        </div>
                    </div>
                )
                break;

            case 2:
                return(
                    <div style={{display:'flex', flexDirection:'column'}}>
                        <div>{cnum} /5</div>
                        <img src={img2} />
                        <div style={{display:'flex', flexDirection:'row'}}>
                            <Button onClick={e => setCnum(cnum - 1)}>이전</Button>
                            <Button onClick={e => setCnum(cnum + 1)}>다음</Button>
                        </div>
                    </div>
                )
                break;

            case 3:
                return(
                    <div style={{display:'flex', flexDirection:'column'}}>
                        <div>{cnum} /5</div>
                        <img src={img3} />
                        <div style={{display:'flex', flexDirection:'row'}}>
                            <Button onClick={e => setCnum(cnum - 1)}>이전</Button>
                            <Button onClick={e => setCnum(cnum + 1)}>다음</Button>
                        </div>
                    </div>
                )
                break;

            case 4:
                return(
                    <div style={{display:'flex', flexDirection:'column'}}>
                        <div>{cnum} /5</div>
                        <img src={img4} />
                        <div style={{display:'flex', flexDirection:'row'}}>
                            <Button onClick={e => setCnum(cnum - 1)}>이전</Button>
                            <Button onClick={handleClose} style={{backgroundColor:'rgba(255,0,0,0.3)'}}>닫기</Button>
                        </div>
                    </div>
                )
                break;
        }
    }

    return (
        <div>
            <StyledModal
                aria-labelledby="unstyled-modal-title"
                aria-describedby="unstyled-modal-description"
                open={open}
                BackdropComponent={Backdrop}
            >
                <Box sx={style}>
                    {content()}
                </Box>
            </StyledModal>
        </div>
    )
}

export default ModalMade
